#root {
  width: 95%;
  height: 100%;
  max-width: 960px;
  margin: 4em auto 0;
}

input {
  width: 100%;
}

* {
  font-family: monospace;
  box-sizing: border-box;
}

.fw {
  width: 100%;
}

textarea {
  resize: none;
}

span {
  overflow-x: hidden;
}

input,
textarea,
button {
  -webkit-appearance: none;
  -moz-appearance: none;
  border-radius: 2px;
  background: none;
  border: solid 1px #aaa;
  transition: all 0.15s ease;
}

input,
textarea {
  padding: 8px 6px;
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.05);
}

input:focus,
textarea:focus {
  border-color: #333;
}

input:hover,
textarea:hover {
  border-color: #333;
  box-shadow: inset 0 1px 2px rgba(167, 87, 87, 0.1);
}

button {
  display: inline-block;
  padding: 8px 6px;
  font-weight: bold;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.1);
  cursor: pointer;
}

button:hover {
  background-color: #efefef;
  border-color: #666;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.2);
}

[disabled] {
  opacity: 0.4;
  color: inherit;
  pointer-events: none;
}

.text-center {
  text-align: center;
}

.title {
  margin: 5px 0 5px;
}

.truncate {
  display: block;
  overflow: hidden;
  text-overflow: ellipsis;
  width: 100%;
}